.chart-container {
  position: relative;
  width: 400px;
  height: 400px;
  border: 2px solid var(--color-dark-gray);
  margin-top: 16px;
}

.chart-container::before {
  content: "";
  border: 2px solid var(--color-light-gray);
  display: flex;
  width: 98%;
  position: absolute;
  bottom: 50%;
}

.chart-container::after {
  content: "";
  border: 2px solid var(--color-light-gray);
  display: flex;
  width: 98%;
  position: absolute;
  bottom: 50%;
  transform: rotate(90deg);
}

.chart-area-label-container {
  display: grid;
  grid-template-columns: 200px 200px;
  grid-template-rows: 200px 200px;
  position: absolute;
}

.chart-area-label-container div span {
  color: var(--color-white);
  background-color: var(--color-light-blue);
  padding: 2px 8px;
  border-radius: 4px;
}

.chart-area-label-container div {
  margin-top: 4px;
}

.chart-area-label-container div:nth-child(even) {
  text-align: right;
  margin-right: 6px;
}

.chart-area-label-container div:nth-child(odd) {
  text-align: left;
  margin-left: 6px;
}

.chart-text {
  font-size: 16px;
  font-weight: 500;
  font-family: sans-serif;
  color: var(--color-dark-gray);
}

.chart-left-text {
  position: absolute;
  bottom: 70px;
  left: -90px;
  transform: rotate(-90deg);
}

.chart-bottom-text {
  position: absolute;
  bottom: -20px;
}

.chart-label-wrapper-paragraph {
  width: 100%;
  display: flex;
  justify-content: center;
}
